@mixin nav_active {
  position: relative;
  &::after {
    opacity: 0;
    content: '';
    position: absolute;
    bottom: -0.4em;
    left: 0.4em;
    right: 0.4em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

  &.on {
    font-weight: bold;
    &::after {
      opacity: 1;
    }
  }
}

@mixin ellipse($line: 2) {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin select-nav($width: 80px) {
  .nav {
    flex: none;
    border-radius: 15px;
    display: flex;
    border: 1px solid #BBBBBB;
    margin: 10px auto;
    width: min-content;

    &_link {
      position: relative;
      flex: none;
      width: $width;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;

      &:hover {
        background-color: #F5F5F5;
      }

      &.on {
        background-color: #BBBBBB;
        color: #fff;
      }

      .lt_ico {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(20%, -50%);
        border: 1px solid #fff;
        border-radius: 1000px;
        background-color: var(--themeColor);
        line-height: 1;
        padding: 2px 4px;
        color: #fff;
      }
    }
  }
}

.domHome {
  &_newest {
    &_nav {
      margin: 0 auto;
      width: 230px;
      height: 30px;
      line-height: 28px;
      border-radius: 15px;
      display: flex;
      border: 1px solid #bbb;
      overflow: hidden;

      &_link {
        width: 115px;
        border-radius: 15px;
        text-align: center;

        &:hover {
          background-color: #F4F4F4;
        }

        &.on {
          background-color: #BBBBBB;
          color: #fff;
        }
      }
    }

    &_sub {
      &_nav {
        padding: 24px 30px 12px;
        display: flex;

        &_link {
          margin-right: 20px;
          cursor: pointer;

          &.on {
            font-weight: bold;
          }
        }


      }

      &_control_center {
        margin-left: auto;
        display: flex;

        .showtype {
          color: #333;

          padding: 0 8px;
          line-height: 20px;
          border-radius: 10px;
          cursor: pointer;

          &:hover {
            opacity: #000;
          }

          &.on {
            background-color: #FDF5F5;
            color: var(--themeColor);

            &:hover {
              background-color: #FDEDED;
            }
          }
        }
      }
    }

    &_song {

      &_list {
        .item {
        }

        .cover {
        }

        .name {
        }

        .artists {
          width: 18%;
        }

        .album {
          width: 18%;
        }

        .duration {
          width: 10%;
        }
      }
    }

    &_album {
      &_list {
        position: relative;
        margin: 0 30px;
      }

      &_sublist {
        position: relative;
        padding-left: 50px;
        margin-bottom: 50px;

        .sign {
          width: 40px;
          height: 40px;
          position: absolute;
          top: 0;
          left: 0;
          font-size: 20px;
        }

        .item {
          width: 140px;
        }

        .cover {
          width: 140px;
          height: 140px;
        }
      }
    }
  }
}
